.App {
  text-align: center;
}

.App-logo {
  width:100%;
  height:100%;
  /* height: 40vmin; */
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.logo-header {
  width:160px;
}

.logo-header-headline {
  padding-bottom: 300px;
  font-size:35px;
}


/* container for search form */
.search-form {
  width:650px;
  height:48px;
  margin:0 auto;
  border-radius: 15px;
  display: flex;
  justify-content: space-between;
  background-color: darkslategrey;
  padding:8px;
  outline:2px solid grey;
}

/* search input box */
.search-box {
  width:88%;
  background-color: rgba(0,0,0,0);
  box-sizing: border-box;
  border:none;
  outline:none;
  border-radius: 15px;
  font-family: "Calibri", Arial, monospace;
  color:rgb(225, 232, 232);
  font-size: calc(10px + 2vmin);
  text-indent:2%;
}

/* submit search button */
.submit-button {
  width: 10%;
  height:100%;
  background-color: rgb(89, 110, 110);
  box-sizing:border-box;
  border: none;
  border-radius: 15px;
  align-self:center;
}

.submit-logo {
  width:100%;
  border-radius: 100%;
}